export class UserData {

    constructor(userData?: UserData) {

        if (userData) {
            for (let prop in userData) {
                if (userData.hasOwnProperty(prop)) {
                    this[prop] = userData[prop];
                }
            }
            
            
        } else {
            
        }
        
    }
    
    firstName: String;

    lastName: String;

    email: String;

    password: String;

    agreedToTerms: boolean;

}