import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { isNullOrUndefined } from 'util';

@Injectable()
export class ProductService {

    constructor(private http: HttpClient) { }

    ngOnInit() {
    }

    getRetailersList() {
        return this.http.get("/api/v1/retailer").pipe(map((res) => {
            return res["Retailers"];
        }));
    }

    getProductsByCategory(category: String) {
        return this.http.get("/api/v1/products/category/" + category).pipe(map((res) => {
            return res["Products"];
        }));
    }

    getProductsBySimilarity(selectedProduct: String) {
        return this.http.post("/api/v1/products/similar",selectedProduct).pipe(map((res) => {
            return res["Products"];
        }));
    }

    getDropDownValues(itemName: String) {
        return this.http.post("/api/v1/dropdown/items",itemName).pipe(map((res) => {
            return res["DropDownValues"];
        }));
    }

    getProductsByWishList() {
        return this.http.get("/api/v1/products/wishlist").pipe(map((res) => {
            return res["Products"];
        }));
    }

    addProductsToWishList(productId: String) {
            return this.http.put("/api/v1/products/wishlist",productId).pipe(map((res) => {
            return res["Product"];
        }));
    }

    removeProductsFromWishList(productId: String) {
            return this.http.delete("/api/v1/products/wishlist/"+productId).pipe(map((res) => {
            return res[""];
    }));
}


    getCategoryData() {
        return this.http.get("/api/v1/products/category/data").pipe(map((res) => {
            return res["Category"];
        }));
    }

    searchProducts(keyword: any, zip: any, category: any , outfitType: any,brandName: any,
                    color: any , priceRange: any,
                    neckType: any,fittingType: any,sleeveLength: any,patternType: any,
                    borderStyle: any,retailerType: any,ornamentation: any) {
        if(isNullOrUndefined(zip)) {
            zip = "";
        }
        if(isNullOrUndefined(category)) {
            category = "";
        }
        if(isNullOrUndefined(keyword)) {
            keyword = "";
        }
        if(isNullOrUndefined(keyword)) {
            keyword = "";
        }
        return this.http.get("/api/v1/products?title="+ encodeURIComponent(keyword) +"&category=" + encodeURIComponent(category)+"&outfitType=" + encodeURIComponent(outfitType)
        +"&brandName=" + encodeURIComponent(brandName)+"&color=" + encodeURIComponent(color)+"&priceRange=" + encodeURIComponent(priceRange)
        +"&neckType=" + encodeURIComponent(neckType)+"&fittingType=" + encodeURIComponent(fittingType)
        +"&sleeveLength=" + encodeURIComponent(sleeveLength)+"&patternType=" + encodeURIComponent(patternType)
        +"&borderStyle=" + encodeURIComponent(borderStyle)+"&retailerType=" + encodeURIComponent(retailerType)
        +"&ornamentation=" + encodeURIComponent(ornamentation)).pipe(map((res) => {
            return res["SearchResult"];
        }));
    }

    getSearchStatus(spIds: String[]) {
        return this.http.post("/api/v1/products/search/", spIds).pipe(map((res) => {
            return res["SearchResult"];
        }));
    }
    
}