import { SupportType } from './SupportType';

export class Support {

    constructor(support?: Support) {

        if (support) {
            for (let prop in support) {
                if (support.hasOwnProperty(prop)) {
                    this[prop] = support[prop];
                }
            }
            
            
        } else {
            
        }
        
    }
    id: String;

    supportType: SupportType;

    email: String;

    message: String;

    createdOn: Date;

}