import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ProductService } from './shared/core/product.service';
import { HttpClientModule } from '@angular/common/http';
import { PageNotFoundComponent } from './shared/pt-components/not-found/not-found.component';
import { AuthenticationService } from './shared/core/authentication.service';
import { SupportService } from './shared/core/support.service';
import { HeaderComponent } from './main/header/header.component';
import { ModalModule } from 'ngx-bootstrap/modal';
import { UserProfileService } from './shared/core/user-profile.service';
import { AvatarModule } from 'ngx-avatar';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { SignupComponent } from './auth/signup/signup.component';
import { FooterComponent } from './main/footer/footer.component';
import { TermsOfUseComponent } from './shared/pt-components/terms-of-use/terms-of-use.component';
import { DisclaimerComponent } from './shared/pt-components/disclaimer/disclaimer.component';
import { PrivacyPolicyComponent } from './shared/pt-components/privacy-policy/privacy-policy.component';
import { DataService } from './shared/core/data.service';
import { ZipcodeService } from './shared/core/zipcode.service';
import { NgSelectModule } from '@ng-select/ng-select';

@NgModule({
  declarations: [
    AppComponent,
    PageNotFoundComponent,
    HeaderComponent,
    SignupComponent,
    FooterComponent,
    TermsOfUseComponent,
    PrivacyPolicyComponent,
    DisclaimerComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    HttpClientModule,
    ModalModule.forRoot(),
    AvatarModule,
    FormsModule,
    NgSelectModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
  ],
  exports: [
    HeaderComponent,
    SignupComponent,
    FooterComponent,
    TermsOfUseComponent,
    PrivacyPolicyComponent,
    DisclaimerComponent
  ],
  providers: [
    ProductService,
    AuthenticationService,
    SupportService,
    UserProfileService,
    DataService,
    ZipcodeService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
