import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { isNullOrUndefined } from 'util';
import { BehaviorSubject } from 'rxjs';
import { UserInfo } from '../models/user-info';
import { PasswordDto } from '../dto/password.dto';

@Injectable()
export class UserProfileService {

    private userInfo = new BehaviorSubject<any>([]);
    currentUserInfo = this.userInfo.asObservable();

    private profilePictureSource = new BehaviorSubject<any>([]);
    currentprofilePicture = this.profilePictureSource.asObservable();

    constructor(private http: HttpClient) { }

    ngOnInit() {
    }

    sendUserInfo(data) {
        this.userInfo.next(data);
    }

    public getUserInfo() {
        return this.http.get("/api/v1/user").pipe(map((res: UserInfo) => {
            //console.log(res);
            //console.log(res["UserInfo"]);
            this.sendUserInfo(res["UserInfo"]);
            sessionStorage.setItem('iuli', res["UserInfo"].email);
            return res["UserInfo"];
        }));
    }

    updateUserProfile(userInfo) {
        return this.http.put("/api/v1/user", userInfo).pipe(map((res) => {
            //console.log(res);
            //console.log(res["UserInfo"]);
            this.sendUserInfo(res["UserInfo"]);
            return res;
        }));
    }

    updateUserPassword(passwordDto: PasswordDto) {
        return this.http.put("/api/v1/reset-password", passwordDto).pipe(map((res) => {
            return res;
        }));
    }

    public uploadProfilePicture(profilePicture) {
        //console.log(profilePicture)
        return this.http.post("/api/v1/profile-picture", profilePicture).pipe(map((res) => {
            this.sendProfilePicture(res["profilePicture"]);
            return res["profilePicture"];
        }));
    }

    sendProfilePicture(data) {
        this.profilePictureSource.next(data);
    }
    
}