export class UserInfo {

    constructor(userInfo?: UserInfo) {

        if (userInfo) {
            for (let prop in userInfo) {
                if (userInfo.hasOwnProperty(prop)) {
                    this[prop] = userInfo[prop];
                }
            }
            
            if(!this.radius) {
                this.radius = 10;
            }
        } else {
            this.radius = 10;
        }
        
    }
    
    firstName: String;

    lastName: String;

    email: String;

    state: String;

    zipCode: String;

    city: String;

    profilePicture: string;

    radius: number;

}